.main {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 1280px;
  padding: 0 20px;
}
.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  list-style-type: none;
  color: rgb(241, 224, 192);
  gap: 8px;
  padding: 0;
  margin: 30px auto;
}

.pageItem {
  cursor: pointer;
}

.pageLink {
  cursor: pointer;
}

.active {
  color: rgb(104, 72, 13);
}
